/**
 * Editor only CSS.
 */
@import "node_modules/@wordpress/base-styles/colors";
@import "node_modules/@wordpress/base-styles/variables";
@import "node_modules/@wordpress/base-styles/mixins";
@import "node_modules/@wordpress/base-styles/breakpoints";
@import "node_modules/@wordpress/base-styles/animations";
@import "node_modules/@wordpress/base-styles/z-index";
@import "node_modules/@wordpress/base-styles/default-custom-properties";

// Undo default editor styles.
// These need extra specificity.
.editor-styles-wrapper .wp-block-kemet-mega-menu {
    ul {
        margin-top: 0;
        margin-bottom: 0;
        margin-left: 0;
        padding-left: 0;
    }

    // Revert any margins.
    // This also makes it work with classic theme auto margins.
    .premium-navigation-item.wp-block {
        margin: revert;
    }
}

// The following rule affects the positioning of the dropdown arrow indicator.
// On the frontend, this element is inline, which makes it look correct. In the editor,
// the label is block, which causes the dropdown indicator to wrap onto a new line.
// Therefore we explicitly set it to inline even in the block editor.
.premium-navigation-item__label {
    display: inline;
}

/**
 * Submenus.
 */

// Ensure sub-menus stay open and visible when a nested block is selected.
.premium-navigation__container.is-parent-of-selected-block {
    visibility: visible;
    opacity: 1;
    overflow: visible;
}

// Low specificity default to ensure background color applies to submenus.
.premium-navigation__container,
.premium-navigation-item {
    background-color: inherit;
}

// Only show the flyout on hover if the parent menu item is selected.
.wp-block-kemet-mega-menu:not(.is-selected):not(.has-child-selected)
    .has-child:hover {
    > .premium-navigation__submenu-container {
        opacity: 0;
        visibility: hidden;
    }
}

// Styles for submenu flyout.
.has-child {
    &.is-selected,
    &.has-child-selected {
        > .premium-navigation__submenu-container {
            display: flex;
            opacity: 1;
            visibility: visible;
        }
    }
}

// Show a submenu when generally dragging (is-dragging-components-draggable) if that
// submenu has children (has-child) and is being dragged within (is-dragging-within).
.is-dragging-components-draggable .has-child.is-dragging-within {
    > .premium-navigation__submenu-container {
        opacity: 1;
        visibility: visible;
    }
}

// IE fix for submenu visibility on parent focus.
.is-editing > .premium-navigation__container {
    visibility: visible;
    opacity: 1;
    display: flex;
    flex-direction: column;
}

.is-dragging-components-draggable
    .premium-navigation-link
    > .premium-navigation__container {
    // Set opacity to 1 to still be able to show the draggable chip.
    opacity: 1;
    visibility: hidden;

    // Show the chip but hide the submenu list.
    .block-editor-block-draggable-chip-wrapper {
        visibility: visible;
    }
}

// Show even when a child is selected. This is an edgecase just for navigation submenus.
.is-editing > .premium-navigation__submenu-container > .block-list-appender {
    display: block;
    position: static;
    width: 100%;

    // Colors need to work in both dark themes, and dark dropdown colors.
    // This customization would be nice to retire in a refactor of the canvas appender button.
    .block-editor-button-block-appender {
        color: $white;
        background: $gray-900;
        padding: 0;
        width: $button-size-small;
        border-radius: $radius-block-ui;
        margin-right: 0;
        margin-left: auto;
    }
}

// Hide when hovering.
.premium-navigation__submenu-container .block-list-appender {
    display: none;
}

/**
 * Colors Selector component
 */

$colors-selector-size: 22px;
.block-library-colors-selector {
    width: auto;

    // Toolbar colors-selector button.
    .block-library-colors-selector__toggle {
        display: block;
        margin: 0 auto;
        padding: 3px;
        width: auto;
    }

    // Button container.
    .block-library-colors-selector__icon-container {
        height: 30px;
        position: relative;
        margin: 0 auto;
        padding: 3px;
        display: flex;
        align-items: center;
        border-radius: 4px;
    }

    .block-library-colors-selector__state-selection {
        margin-left: auto;
        margin-right: auto;

        // colors-selector - selection status.
        border-radius: $colors-selector-size * 0.5;
        box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.2);

        width: $colors-selector-size;
        min-width: $colors-selector-size;
        height: $colors-selector-size;
        min-height: $colors-selector-size;
        line-height: ($colors-selector-size - 2);
        padding: 2px;

        > svg {
            // Override `min-width: $button-size-small` on toolbar-group/style.scss
            min-width: auto !important;
        }

        // Styling icon color.
        &.has-text-color {
            > svg,
            > svg path {
                color: inherit;
            }
        }
    }
}

// Colors Selector Popover.
$color-control-label-height: 20px;
.block-library-colors-selector__popover {
    .color-palette-controller-container {
        padding: 16px;
    }

    .components-base-control__label {
        height: $color-control-label-height;
        line-height: $color-control-label-height;
    }

    .component-color-indicator {
        float: right;
        margin-top: 2px;
    }

    .components-panel__body-title {
        display: none;
    }
}

// Override inner padding on default appender.
// This should be a temporary fix, to be replaced by improvements to
// the sibling inserter.
// See https://github.com/WordPress/gutenberg/issues/37572.
.wp-block-kemet-mega-menu .block-editor-button-block-appender {
    background-color: $gray-900;
    color: $white;

    // This needs specificity to override an inherited padding.
    // That source padding in turn has high specificity to protect
    // from theme CSS bleed.
    &.block-editor-button-block-appender.block-editor-button-block-appender {
        padding: 0;
    }
}

.wp-block-kemet-mega-menu
    .wp-block
    .wp-block
    .block-editor-button-block-appender {
    background-color: transparent;
    color: $gray-900;
}

/**
 * Setup state
 */

// Loading state.
@keyframes loadingpulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}
// Unstyle some inherited placeholder component styles.
.components-placeholder.components-placeholder.premium-navigation-placeholder {
    outline: none;
    padding: 0;
    box-shadow: none;
    background: none;
    min-height: 0;

    // Needed for the preview menu items to match actual menu items.
    .components-placeholder__fieldset {
        font-size: inherit;
    }

    .components-placeholder__fieldset .components-button {
        margin-bottom: 0;
    }

    // For the placeholder indicators to colorize correctly, colors need to be inherited unless selected.
    color: inherit;

    .wp-block-kemet-mega-menu.is-selected & {
        color: $gray-900;
    }
}

.premium-navigation-placeholder .components-spinner {
    margin-top: 0;
}

// Unselected state.
.premium-navigation-placeholder__preview {
    display: flex;
    align-items: center;
    min-width: $grid-unit-10 * 12;
    font-size: $default-font-size;
    font-family: $default-font;

    .wp-block-kemet-mega-menu.is-selected & {
        display: none;
    }

    // Draw the dashed outline.
    // By setting the dashed border to currentColor, we ensure it's visible
    // against any background color.
    color: currentColor;
    background: transparent;
    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border: $border-width dashed currentColor;
        opacity: 0.4;
        pointer-events: none;

        // Inherit border radius from style variations.
        border-radius: inherit;
    }

    > svg {
        fill: currentColor;
        opacity: 0.4;
    }
}

// Prevent layout shift for vertical variant.
.wp-block-kemet-mega-menu.is-vertical .is-small,
.wp-block-kemet-mega-menu.is-vertical .is-medium {
    .components-placeholder__fieldset {
        // The size of two buttons, with top and bottom padding, and gap.
        min-height: ($button-size * 2) + $grid-unit-15 + ($grid-unit-15 * 0.5);
    }
}

.wp-block-kemet-mega-menu.is-vertical .is-large {
    .components-placeholder__fieldset {
        // The size of three buttons, with top and bottom padding, and gap.
        min-height: ($button-size * 3) + $grid-unit-15 + $grid-unit-15;
    }
}

// Selected state.
.premium-navigation-placeholder__preview,
.premium-navigation-placeholder__controls {
    padding: ($grid-unit-15 * 0.5) $grid-unit-10;
    flex-direction: row;
    align-items: flex-start;
}

.premium-navigation-placeholder__controls {
    border-radius: $radius-block-ui;
    background-color: $white;
    box-shadow: inset 0 0 0 $border-width $gray-900;
    display: none;
    position: relative;
    z-index: 1;

    // Show when selected.
    .wp-block-kemet-mega-menu.is-selected & {
        display: flex;
    }

    // If an ancestor has a text-decoration property applied, it is inherited regardless of
    // the specificity of a child element. Only pulling the child out of the flow fixes it.
    // See also https://www.w3.org/TR/CSS21/text.html#propdef-text-decoration.
    float: left;

    // Hide the navigation indicator when in small contexts.
    .is-small &,
    .is-medium & {
        .premium-navigation-placeholder__actions__indicator,
        .premium-navigation-placeholder__actions__indicator + hr {
            display: none;
        }
    }

    // Vertical orientation or small context.
    .is-small &,
    .wp-block-kemet-mega-menu.is-vertical & {
        .premium-navigation-placeholder__actions {
            flex-direction: column;
            align-items: flex-start;

            hr {
                display: none;
            }
        }
    }

    .premium-navigation-placeholder__icon {
        margin-right: $grid-unit-15;
        height: $button-size; // Prevents jumpiness.
    }
}

// Block title
.premium-navigation-placeholder__actions__indicator {
    display: flex;
    padding: 0 ($grid-unit-15 * 0.5) 0 0;
    align-items: center;
    justify-content: flex-start;
    line-height: 0;
    height: $button-size;

    // Line up with the icon in the toolbar.
    margin-left: $grid-unit-05;

    svg {
        margin-right: $grid-unit-05;
        fill: currentColor;
    }
}

// Keep as row for medium.
.wp-block-kemet-mega-menu
    .components-placeholder.components-placeholder.is-medium
    .components-placeholder__fieldset {
    flex-direction: row !important;
}

.premium-navigation-placeholder__actions {
    display: flex;
    font-size: $default-font-size;
    font-family: $default-font;
    gap: $grid-unit-15 * 0.5;
    align-items: center;

    // Margins.
    .components-dropdown,
    > .components-button {
        margin-right: 0;
    }

    // Separator.
    height: 100%; // This allows the separator to scale vertically.

    // Separator.
    &.premium-navigation-placeholder__actions hr {
        border: 0;
        min-height: $border-width;
        min-width: $border-width;
        background-color: $gray-900;
        margin: auto 0;
        height: 100%;
        max-height: $grid-unit-20;
    }
}

/**
 * Mobile menu.
 */

// These needs extra specificity in the editor.
.premium-navigation__responsive-container:not(.is-menu-open) {
    .components-button.premium-navigation__responsive-container-close {
        @include break-small {
            display: none;
        }
    }
}
// Emulate the fullscreen editing inside the editor.
// Most of this can be removed when the iframe lands.

// When not fullscreen.
.premium-navigation__responsive-container.is-menu-open {
    position: fixed;
    top: $admin-bar-height-big + $header-height + $block-toolbar-height +
        $border-width;

    @include break-medium() {
        top: $admin-bar-height + $header-height + $border-width;
    }

    // Navigation sidebar rules.
    @include break-medium() {
        left: $admin-sidebar-width-collapsed;
    }
    @include break-large() {
        left: $admin-sidebar-width;
    }
}

.has-fixed-toolbar .premium-navigation__responsive-container.is-menu-open {
    @include break-medium() {
        top: $admin-bar-height + $header-height + $block-toolbar-height +
            $border-width;
    }
}

.is-mobile-preview .premium-navigation__responsive-container.is-menu-open,
.is-tablet-preview .premium-navigation__responsive-container.is-menu-open {
    top: $admin-bar-height + $header-height + $block-toolbar-height +
        $border-width;
}

.is-sidebar-opened .premium-navigation__responsive-container.is-menu-open {
    right: $sidebar-width;
}

// When fullscreen.
.is-fullscreen-mode {
    .premium-navigation__responsive-container.is-menu-open {
        left: 0; // Unset the value from non fullscreen mode.
        top: $admin-bar-height-big + $header-height + $block-toolbar-height +
            $border-width;

        @include break-medium() {
            top: $header-height + $border-width;
        }
    }

    .has-fixed-toolbar .premium-navigation__responsive-container.is-menu-open {
        @include break-medium() {
            top: $header-height + $block-toolbar-height + $border-width;
        }
    }

    .is-mobile-preview .premium-navigation__responsive-container.is-menu-open,
    .is-tablet-preview .premium-navigation__responsive-container.is-menu-open {
        top: $header-height + $block-toolbar-height + $border-width;
    }
}

// The iframe makes these rules a lot simpler.
body.editor-styles-wrapper
    .premium-navigation__responsive-container.is-menu-open {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

// Without this, the block cannot be selected, nor does the right container get focus.
// @todo: this is disruptive. Ideally we can retire a few of the containers,
// so focus is applied naturally on the block container.
// It's important the right container has focus, otherwise you can't press
// "Delete" to remove the block.
.premium-navigation__responsive-close {
    @include break-small() {
        pointer-events: none;

        .premium-navigation__responsive-container-close,
        .block-editor-block-list__layout * {
            pointer-events: all;
        }
    }

    // Page List items should remain inert.
    .wp-block-pages-list__item__link {
        pointer-events: none;
    }
}

// The menu and close buttons need higher specificity in the editor.
.components-button.premium-navigation__responsive-container-open.premium-navigation__responsive-container-open,
.components-button.premium-navigation__responsive-container-close.premium-navigation__responsive-container-close {
    padding: 0;
    height: auto;
    color: inherit;
}

// Customize the mobile editing.
// This can be revisited in the future, but for now, inherit design from the parent.
.is-menu-open .premium-navigation__responsive-container-content * {
    .block-list-appender {
        margin-top: $grid-unit-20;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

// Space spinner to give it breathing
// room when block is selected and has focus outline.
.wp-block-kemet-mega-menu .components-spinner {
    padding: $grid-unit-10 $grid-unit-15;
}

.premium-navigation__unsaved-changes {
    position: relative;

    .components-spinner {
        position: absolute;
        top: calc(50% - #{$spinner-size} / 2);
        left: calc(50% - #{$spinner-size} / 2);

        // Delay showing the saving spinner until after 2 seconds.
        // This should ensure it only shows for slow connections.
        opacity: 0;
        animation: 0.5s linear 2s normal forwards fadein;
    }
}

@keyframes fadeouthalf {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0.5;
    }
}

.premium-navigation__unsaved-changes-overlay.is-saving {
    opacity: 1;
    animation: 0.5s linear 2s normal forwards fadeouthalf;
}

.premium-navigation-delete-menu-button {
    width: 100%;
    justify-content: center;
    margin-bottom: $grid-unit-20;
}

.premium-navigation__overlay-menu-preview {
    display: flex;
    align-items: center;
    width: 100%;
    background-color: $gray-100;
    padding: 0 $grid-unit-30;
    height: $grid-unit-40 * 2;
    margin-bottom: $grid-unit-15;

    &.open {
        box-shadow: inset 0 0 0 $border-width $gray-200;
        outline: 1px solid transparent; // Box shadow is removed in high contrast mode, a transparent outline is made opaque.
        background-color: $white;
    }
}

// Temp - hide toolbar component when Nav Menu Selector is empty.
// TODO - extract state to common hook or parent component to avoid render.
.premium-navigation__toolbar-menu-selector.components-toolbar-group:empty {
    display: none;
}

// Temp - hide adjacent <hr>s when Nav Menu Selector is empty.
// TODO - extract state to common hook or parent component to avoid render of <hr>.
.premium-navigation-placeholder__actions hr + hr {
    display: none;
}
